import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { StyleguideComponent } from './styleguide/styleguide.component';
import { NavbarComponent } from './navbar/navbar.component';
import { FooterComponent } from './footer/footer.component';
import { HomePageComponent } from './home-page/home-page.component';
import { HomeMainComponent } from './home-main/home-main.component';
import { HomeLisComponent } from './home-lis/home-lis.component';
import { HomeTelematicaComponent } from './home-telematica/home-telematica.component';
import { HomeMovilisComponent } from './home-movilis/home-movilis.component';
import { LisDetailsComponent } from './lis-details/lis-details.component';
import { TelematicaDetailsComponent } from './telematica-details/telematica-details.component';
import { MovilisDetailsComponent } from './movilis-details/movilis-details.component';
import { LoginComponent } from './login/login.component';
import { ChangePasswordComponent } from './change-password/change-password.component';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'home', component: HomePageComponent },
  { path: 'lis', component: LisDetailsComponent },
  { path: 'telematica', component: TelematicaDetailsComponent },
  { path: 'movil-is', component: MovilisDetailsComponent },
  { path: 'login', component: LoginComponent },
  { path: 'change-pass', component: ChangePasswordComponent },
  { path: 'styleguide', component: StyleguideComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    StyleguideComponent,
    NavbarComponent,
    FooterComponent,
    HomePageComponent,
    HomeMainComponent,
    HomeLisComponent,
    HomeTelematicaComponent,
    HomeMovilisComponent,
    LisDetailsComponent,
    TelematicaDetailsComponent,
    MovilisDetailsComponent,
    LoginComponent,
    ChangePasswordComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



